.modal{
    position: fixed !important;
    width: 300px;
    border-radius: 3px;
    background: $ff;
    font-size: 14px;
    z-index: 3;
    @include center;
    @include modal-shadow;

    & .modal_no{
        position: relative;
        top: 30px;
        text-align: center;
        
        & img{
            width: 150px;
        }
    }

    & .modal_header{
        background: $header;
        border-bottom: 1px solid $header_border;
        border-top-left-radius: 3px;
        border-top-right-radius: 3px;
        padding: 10px;
        text-align: initial;

        & span.title{
            font-weight: 600;
            color: $dark;
        }
    }

    & .modal_middle{
        position: relative;
        padding: 10px;

        input[type='text'],
        & textarea {
          width: 94%;
          padding: 7px;
          font-size: 14px;
        }

        & textarea {
            height: 150px;
        }
    }

    & .modal_bottom {
        display: block;
        text-align: right;
        padding: 9px 5px 12px;
        border-top: 1px solid $ee;
        margin-top: 0px;

        & span.emoji_span{
            position: absolute;
            display: inline-block;
            height: 24px;
            left: 10px;
            bottom: 14px;
            color: $d_light;
            cursor: pointer;

            &:hover{
                color: $dark;
            }
        }

        & a, & input[type='submit']{
            display: inline-block;
            padding: 4px 10px;
            font-weight: 600;
        }

        & a:last-of-type{
            margin-left: 4px;
        }
    }
}

 //Modal big
  .modal_big{
      width: 400px;

      & .modal_middle{
          height: 450px;
          width: inherit;
          overflow: auto;
          padding: 0px !important;

         & .modal_main{
             position: relative;
             padding: 7px 0px;

           & .modal_items{
               margin-bottom: 5px;
               position: relative;
               padding: 0px 5px 5px;

               & .modal_it_img{
                   position: relative;
                   display: inline-block;
                   height: 45px;
                   margin-left: 5px;

                   & img{
                       width: 45px;
                       height:45px;
                       border-radius: 3px;
                   }
               }

               & .modal_it_content{
                   position: relative;
                   display: inline-block;
                   width: 83%;
                   margin-left: 8px;
                   top: -10px;

                   & .modal_it_info{
                       display: inline-block;
                       width: 100%;

                       & .modal_it_username{
                           font-size: 14px;
                           font-weight: 600;
                           color: $dark;
                           outline: none;

                           &:hover,
                           &:focus{
                               text-decoration: underline;
                           }
                       }

                       & span.modal_it_light{
                           display: block;
                           color:$d_light;
                           font-size: 13px;
                       }
                   }

                   & .modal_ff{
                       position: absolute;
                       display: inline-block;
                       right: 5px;
                       top: 5px;
                       text-align: right;

                       & a{
                           display: inline-block;
                           padding: 5px 15px;
                           font-weight: 600;
                       }
                   }
               }

               & hr{
                   border-top-color: $ee;
                   margin-top: 3px;
               }
           }  
         
         & .already_shared{
             background: $fb !important;
             cursor: not-allowed !important;
         }
      }
  }

  & .modal_bottom{
      margin-top: 1px;

      & a:last-of-type{
          margin-right: 10px;
          margin-left: 5px;
      }
  }

  & .spinner{
      top: 175px !important;
      left: 200px !important;
  }
}
















































