body {
    background-color: #eee;
 }
  
  #wrapper {
    width: 500px;
    height: 50%;
    overflow: hidden;
    border: 0px solid #000;
    margin: 50px auto;
    padding: 10px;


    .main-content {
        width: 250px;
        height: 40%;
        margin: 10px auto;
        background-color: #fff;
        border: 2px solid #e6e6e6;
        padding: 40px 50px;
    
        .header {
            position: relative;
            border: 0px solid #000;
            margin-bottom: 5px;
        
            & img {
                height: 50px;
                width: 175px;
                margin: auto;
                position: relative;
                left: 40px;
              }
          }
          
          .input-1,
          .input-2 {
            width: 100%;
            margin-bottom: 5px;
            padding: 8px 12px;
            border: 1px solid #dbdbdb;
            box-sizing: border-box;
            border-radius: 3px;
          }
          
          .overlap-text {
            position: relative;
        
            & a {
                position: absolute;
                top: 8px;
                right: 10px;
                color: #003569;
                font-size: 14px;
                text-decoration: none;
                font-family: 'Overpass Mono', monospace;
                letter-spacing: -1px;
              }
          }
                       
          .btn {
            width: 100%;
            background-color: #3897f0;
            border: 1px solid #3897f0;
            padding: 5px 12px;
            color: #fff;
            font-weight: bold;
            cursor: pointer;
            border-radius: 3px;
          }
      }
      
      .sub-content {
        width: 250px;
        height: 40%;
        margin: 10px auto;
        border: 1px solid #e6e6e6;
        padding: 20px 50px;
        background-color: #fff;

        .s-part {
            text-align: center;
            font-family: 'Overpass Mono', monospace;
            word-spacing: -3px;
            letter-spacing: -2px;
            font-weight: normal;
    
            & a {
                text-decoration: none;
                cursor: pointer;
                color: #3897f0;
                font-family: 'Overpass Mono', monospace;
                word-spacing: -3px;
                letter-spacing: -2px;
                font-weight: normal;
              }
          }
      }
          
      input:focus {
          background-color: #fafafa;
      }
      
  }
  

  
